
import { defineComponent, computed } from 'vue'

export default defineComponent({
  name: 'BODatePicker',
  inheritAttrs: false,
  props: {
    modelValue: {
      type: [String, Date, Object],
      required: true
    }
  },
  setup(props, { emit }) {
    const renderDateValue = computed({
      get() {
        return props.modelValue
      },
      set(v) {
        emit('update:modelValue', v)
      }
    })
    return {
      renderDateValue
    }
  }
})
